(function(module) {
try {
  module = angular.module('cross');
} catch (e) {
  module = angular.module('cross', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/includes/tag_form.html',
    '<div class="col-xs-12 listitem" ng-controller="TagFormCtrl"><h3 class="text-danger" ng-show="error.error">{{error.msg}}</h3><form ng-submit="tagSubmit()"><div class="form-group"><label for="{{\'companies\' + article.id}}">COMPANY:</label><select ng-model="newTag.company" class="form-control" ng-options="company.id as company.name for company in companies"><option value="">---- Select Company ----</option></select></div><div class="form-group"><label for="{{\'companies\' + article.id}}">CATEGORY:</label><select ng-model="newTag.subcategory" class="form-control" ng-options="category.id as category.name group by category.category for category in categories" ng-change="loadFacts()"><option value="">---- Select Ethics Category ----</option></select></div><div class="form-group"><label for="{{\'companies\' + article.id}}">TAG TYPE:</label><select ng-model="newTag.tag_type" class="form-control" ng-options="tagType.id as tagType.name for tagType in tagTypes"><option value="">---- Select Tag Type ----</option></select><p class="help-block">Don\'t see the tag you want? <a ng-click="showNewTagType($event)">Add it!</a></p></div><div class="form-group" ng-show="tagFormState.addTagType"><label for="{{\'newTagType\' + article.id}}">NEW TAG TYPE:</label><input ng-model="newTagType.name" class="form-control" placeholder="Enter Your New Tag Here"> <button class="btn btn-edit btn-block" ng-click="submitTagType($event)">Add Tag Type</button></div><div class="form-group" ng-show="newTag.tag_type"><label for="{{\'NewTagValue\' + article.id}}">VALUE (Only for relevant tags):</label><input ng-model="newTag.value" class="form-control" placeholder="Enter amount for tag modifier"></div><div class="form-group"><label for="{{\'crossNotes\' + article.id}}">EXCERPT:</label><textarea rows="3" ng-model="newTag.excerpt" name="notes" id="{{\'excerpt\' + article.id}}" class="form-control"></textarea></div><input type="submit" class="btn btn-primary btn-block" value="Submit"></form><button class="btn btn-delete btn-block" ng-click="tagCancel()">Cancel</button></div>');
}]);
})();
