(function(module) {
try {
  module = angular.module('cross');
} catch (e) {
  module = angular.module('cross', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/articles.html',
    '<div class="container" id="taggedArticles"><div class="taggedArticles row"><h1>Analyzed Articles</h1><div class="listitem col-xs-12" ng-repeat="article in taggedArticles"><div class="article listitem col-xs-12" ng-controller="SingleArticleCtrl"><div class="col-xs-12" ng-include="articleTemplate"></div><div class="col-xs-12 tag" ng-repeat="tag in article.tags" ng-controller="SingleTagCtrl" ng-mouseenter="buttons=true" ng-mouseleave="buttons=false"><div ng-include="tagUrl"></div></div></div></div></div></div><div class="container" id="articles"><div class="articleList row"><h1>Add New article</h1><div class="col-xs-12 listitem" ng-controller="NewArticleCtrl"><div ng-include="articleForm"></div></div><h1>Unanalyzed Articles</h1><div class="article listitem col-xs-12" ng-repeat="article in articles" ng-controller="SingleArticleCtrl"><div class="col-xs-12" ng-include="articleTemplate"></div></div></div></div>');
}]);
})();
